body {
  background: #f4dce066;
}

nav {
  background: #9b6ea5;
  position: sticky;
  top: 1px;
}

nav ul {
  padding: 7px 0;
  margin-top: 0;
  list-style: none;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

nav ul li a  {
  color: #000;
}

h1 {
  text-align: center;
}

figure, img {
  margin: 0;
  width: 100%;
}

h2, h3 {
  margin-left: 15px;
}

.caracteristicas ul li {
  margin: 10px 5px;
}

.promociones {
  display: flex;
  flex-direction: column;
  margin: 30px 0;
}

.promociones ul {
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  text-align: center;
  background: #9b6ea533;
  border-radius: 10px;
  padding: 10px 0;
}

.promociones ul img, .promociones ul figure {
  width: 45vw;
}

.reservacion {
  margin: 10px 15px;
}

.reservacion h3 {
  margin-left: 0;
}

.reservacion form {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  height: 200px;
}

.reservacion form label {
  width: 50vw;
}

.reservacion form input {
  width: 35vw;
}

.reservacion form button {
  padding: 5px;
  background: #9b6ea577;
}

.reservacion form button:hover {
  background: #9b6ea5;
}

.informacion {
  margin: 10px;
}

.articleHabitacion {
  background: #0001;
  border-radius: 10px;
}

.articleHabitacion p {
  margin: 10px;
  line-height: 1.5;
}

.articleHabitacion img {
  height: 200px;
  object-fit: cover;
}

.articleHabitacion svg {
  width: 35px;
  height: 35px;
}

.articleHabitacion .izquierda {
  position: relative;
  left: 20px;
}

.articleHabitacion .derecha {
  position: absolute;
  right: 20px;
}

.socios, .convenios {
  margin: 30px 0;
}

.socios ul, .convenios ul {
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.socios ul li, .convenios ul li {
  margin: 10px;
}

.pie {
  padding: 10px;
  text-align: center;
  background: linear-gradient(transparent, #9b6ea5);
  border-radius: 20px 10px;
}

.pie h4 {
  margin: 0;
}

.paginaError p {
  margin: 16px 20px;
  text-align: center;
}

@media only screen and (min-width: 700px) {
  .promociones {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
  }

  .promociones h3 {
    width: 100vw;
  }

  .promociones ul {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 30vw;
  }

  .promociones ul figure, .promociones ul img {
    width: 90%;
  }

  .reservacion form {
    flex-direction: column;
    width: 50vw;
    margin: auto;
  }

  .reservacion form input {
    width: 100%;
  }

  .reservacion form button {
    width: 50%;
  }

  .articleHabitacion h3 {
    text-align: center;
  }

  .articleHabitacion figure, .articleHabitacion img {
    height: 400px;
  }

  .historia, .socios, .convenios {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
  }

  .historia h3, .socios h3, .convenios h3 {
    width: 100vw;
  }

  .historia figure, .historia img, .historia p {
    width: 47vw;
    margin: 0;
  }

  .socios figure, .socios img, .convenios figure, .convenios img, .socios ul, .convenios ul {
    width: 47vw
  }

  .socios h3 {
    order: 1;
  }

  .socios figure {
    order: 3;
  }

  .socios ul {
    order: 2;
  }

  .paginaError figure, .paginaError img {
    width: 30vw;
    margin: auto;
  }
}

@media only screen and (min-width: 1000px) {
  .reservacion form {
    width: initial;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
  }

  .reservacion form label, .reservacion form input, .reservacion form button {
    width: initial;
  }

  .articleHabitacion {
    width: 80vw;
    margin: auto;
  }

  .articleHabitacion figure, .articleHabitacion img {
    height: 600px;
  }

  .articleHabitacion .izquierda {
    left: 45%;
    margin: 10px 0;
  }

  .articleHabitacion .derecha {
    right: 45%;
    margin: 10px 0;
  }

  main {
    margin: 50px;
  }

  .historia figure, .historia img {
    width: 30vw;
  }

  .socios figure, .socios img, .convenios figure, .convenios img {
    width: 30vw;
  }

  
}